html {
  font-family: "Roboto", sans-serif;
}

body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;

  width: 200px;
  padding: 32px 16px 16px;
  border-radius: 5px;
  border: 1px solid #f3f3f3;

  &__image {
    width: 160px;
    height: 134px;
    margin: 0 3px 40px;
  }

  &__title {
    color: $mainColor;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    margin: 0 0 4px;
  }

  &__code {
    color: $secondColor;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    margin: 0 0 16px;
  }

  &__reviews-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }

  &__reviews {
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    margin: 0;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 0 16px;
  }

  &__price-text {
    color: $secondColor;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    margin: 0;
  }

  &__price-amount {
    color: $mainColor;
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
    margin: 0;
  }

  &__buy-button {
    color: $bgcolor;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 14px;
    font-weight: 700;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border-radius: 5px;
    background-color: $buttonColor;
  }

  &__buy-button:hover {
    color: $buttonColor;
    border: 1px solid $buttonColor;
    background-color: $bgcolor;
  }
}
